import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useAdmissionOngoingApplicationReject } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
// import FormInput from "../../../../Ui/Input/FormInput";
import FormTextarea from "../../../../Ui/Input/FormTextarea";
import { useLocation } from "react-router-dom";
import { useDepartmentOngoingApplicationReject } from "../../../../Hooks/ApiHooks/DepartmentManager/department-manager-api";
const AdmissionApplicationReject = ({
  onClose,
  sid,
  appId,
  onRefetch,
  isProfile,
  setWhichSelect,
  staffID,
  isDepartment,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [state, setState] = useState({
    // query: "",
    reason: "",
  });

  const [admissionOngoingApplicationReject] =
    useAdmissionOngoingApplicationReject();
  const [departmentOngoingApplicationReject] =
    useDepartmentOngoingApplicationReject();
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onReject = () => {
    let validation = requiredField(state);
    if (existingRequiredField(validation)) {
      // if (state.query === "Confirm" && appId && sid) {
      if (appId && sid) {
        setDisabled((pre) => !pre);
        if (isDepartment) {
          departmentOngoingApplicationReject({
            sid: sid,
            appId: appId,
            bodyParameter: {
              ...state,
              staffId: staffID ? staffID : getQuery.state?.sid,
            },
          })
            .then(() => {
              if (isProfile) {
                setWhichSelect(false);
              } else {
                onRefetch();
              }
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch(() => {
              setDisabled((pre) => !pre);
            });
        } else {
          admissionOngoingApplicationReject({
            sid: sid,
            appId: appId,
            bodyParameter: {
              ...state,
              staffId: staffID ? staffID : getQuery.state?.sid,
            },
          })
            .then(() => {
              if (isProfile) {
                setWhichSelect(false);
              } else {
                onRefetch();
              }
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch(() => {
              setDisabled((pre) => !pre);
            });
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("cancel_admission_application")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            {/* <FormInput
              label={t("type_confirm_to_continue")}
              placeholder={t("type_confirm_to_continue")}
              name="query"
              value={state.query}
              type="text"
              onChange={onInputChange}
              errorShow={errorField["query"]}
            /> */}

            <FormTextarea
              label={t("reason")}
              placeholder={t("please_enter_reject_reason")}
              name="reason"
              value={state.reason}
              type="text"
              onChange={onInputChange}
              errorShow={errorField["reason"]}
            />

            <CreateButton label="confirm" onAction={onReject} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionApplicationReject;
